import { Helmet } from 'react-helmet-async';
import PageHeader from './PageHeader';
import PageTitleWrapper from 'src/components/PageTitleWrapper';
import { Container, Grid, Typography } from '@mui/material';
import Footer from 'src/components/Footer';

import Content from './Content';
import { useGetUsersMeQuery } from 'src/services/baseApi';

function ManagementUserSettings() {

  const { data, isLoading, isSuccess  } = useGetUsersMeQuery(
    null, { refetchOnMountOrArgChange :true, refetchOnFocus: true }
  );

  if (isLoading) return (
    <Container maxWidth="lg">
      <Grid container spacing={3}>
        <Grid item xs={12}>
          <Typography>Carregando...</Typography>
        </Grid>
      </Grid>
    </Container>
  );

  if (!data) return (
    <Container maxWidth="lg">
      <Grid container spacing={3}>
        <Grid item xs={12}>
          <Typography>Nenhum dado de usu√°rio encontrado.</Typography>
        </Grid>
      </Grid>
    </Container>
  );

  return (
    <>
      <Helmet>
        <title>Minha Conta</title>
      </Helmet>
      <PageTitleWrapper>
        <PageHeader />
      </PageTitleWrapper>
      <Container maxWidth="lg">
        <Grid
          container
          direction="row"
          justifyContent="center"
          alignItems="stretch"
          spacing={3}
        >
          <Grid item xs={12}>
            <Content
              data={data}
            />
          </Grid>
        </Grid>
      </Container>
      <Footer />
    </>
  );
}

export default ManagementUserSettings;
