import {
  Grid,
  Typography,
  CardContent,
  Card,
  Box,
  Divider,
  Avatar,
  Tooltip,
  IconButton,
  lighten,
  styled
} from '@mui/material';
import { useState, Fragment } from 'react';
import PersonIcon from '@mui/icons-material/Person';
import EditTwoToneIcon from '@mui/icons-material/EditTwoTone';

import FormDialogEdit from 'src/content/applications/inventory/Home/FormDialogEdit';

import Text from 'src/components/Text';
import { Usuario } from 'src/models/types';
import { ModelUsuario } from 'src/models/schemas';

const AvatarPrimary = styled(Avatar)(
  ({ theme }) => `
      background: ${theme.colors.primary.lighter};
      color: ${theme.colors.primary.main};
      width: ${theme.spacing(6)};
      height: ${theme.spacing(6)};
`
);

const IconButtonEdit = styled(IconButton)(
  ({ theme }) => `
      background: ${theme.colors.secondary.lighter};
      color: ${theme.colors.secondary.main};
      padding: ${theme.spacing(0.5)};

      &:hover {
      background: ${lighten(theme.colors.secondary.lighter, 0.4)};
      }
  `
);

function Content(props) {
  
  const usuario = props.data as Usuario;

  const [openDialogUsuario, setOpenDialogUsuario] = useState(false);

  const handleCloseDialogUsuario = () => {
    setOpenDialogUsuario(false);
  };

  return (
    <Grid container spacing={3}>

      {/* ----------------- USU√ÅRIO -----------------  */}
      <Grid item xs={12}>
        <Card>
          <Box
            p={3}
            display="flex"
            alignItems="center"
            justifyContent="space-between"
          >
            <AvatarPrimary>
              <PersonIcon />
            </AvatarPrimary>
            <Box pl={2} width={"100%"}>
              <Typography variant="h4" gutterBottom>
                Dados Pessoais
              </Typography>
              <Typography variant="subtitle2">
                Edite seus dados pessoais
              </Typography>
            </Box>
            <Tooltip arrow title="Editar">
                <IconButtonEdit sx={{ ml: 1 }} onClick={() => setOpenDialogUsuario(true)}>
                    <EditTwoToneIcon fontSize="small" />
                    <Typography fontSize="small">Editar</Typography>
                </IconButtonEdit>
            </Tooltip>
          </Box>

          <Divider />

          <CardContent sx={{ p: 4 }}>
            <Typography variant="subtitle2">
              <Grid container spacing={1}>

                <Grid item xs={12} sm={4} md={3} textAlign={{ sm: 'right' }}>
                  <Box pr={3} pt={0} pb={0}>
                    Usu√°rio:
                  </Box>
                </Grid>
                <Grid item xs={12} sm={8} md={9}>
                  <Text color="black">
                    <b>{usuario.nome}</b>
                  </Text>
                </Grid>

                <Grid item xs={12} sm={4} md={3} textAlign={{ sm: 'right' }}>
                  <Box pr={3} pt={0} pb={0}>
                    Email:
                  </Box>
                </Grid>
                <Grid item xs={12} sm={8} md={9}>
                  <Text color="black">
                    <b>{usuario.email}</b>
                  </Text>
                </Grid>

                <Grid item xs={12} sm={4} md={3} textAlign={{ sm: 'right' }}>
                  <Box pr={3} pt={0} pb={0}>
                    Telefone:
                  </Box>
                </Grid>
                <Grid item xs={12} sm={8} md={9}>
                  <Text color="black">
                    <b>{usuario.fone}</b>
                  </Text>
                </Grid>

                <Grid item xs={12} sm={4} md={3} textAlign={{ sm: 'right' }}>
                  <Box pr={3} pt={0} pb={0}>
                    Fun√ß√£o:
                  </Box>
                </Grid>
                <Grid item xs={12} sm={8} md={9}>
                  <Text color="black">
                    <b>{usuario.funcao}</b>
                  </Text>
                </Grid>

                <Grid item xs={12} sm={4} md={3} textAlign={{ sm: 'right' }}>
                  <Box pr={3} pt={0} pb={0}>
                    Endere√ßo:
                  </Box>
                </Grid>
                <Grid item xs={12} sm={8} md={9}>
                  <Text color="black">
                    <b>{usuario.endereco.rua}, {usuario.endereco.cidade} - {usuario.endereco.estado}, {usuario.endereco.pais}</b>
                  </Text>
                </Grid>

              </Grid>
            </Typography>
          </CardContent>
        </Card>
      </Grid>

      <FormDialogEdit
          open={openDialogUsuario}
          title="Cliente"
          jsonInfo={{
            data: JSON.stringify(usuario),
            model: JSON.stringify(ModelUsuario),
            saveProperty: "cliente",
            savePropertyIsArray: false,
            saveId: null,
            // replaces: JSON.stringify(jsonDataReplaces),
            // relationProperties: JSON.stringify(bensRelacionados),
            // relationOptions: JSON.stringify(familiaresRelacionados),
          }}

          onSave={props.onSave}
          onClose={handleCloseDialogUsuario}
      />


      {/* ----------------- COBRAN√áAS -----------------  */}
      <Grid item xs={12}>
        <Card>
          <Box
            p={3}
            display="flex"
            alignItems="center"
            justifyContent="space-between"
          >
            <AvatarPrimary>
              <PersonIcon />
            </AvatarPrimary>
            <Box pl={2} width={"100%"}>
              <Typography variant="h4" gutterBottom>
                Cobran√ßas
              </Typography>
              <Typography variant="subtitle2">
                Hist√≥rico de cobran√ßas
              </Typography>
            </Box>
          </Box>

          <Divider />

          <CardContent sx={{ p: 4 }}>
            <Typography variant="subtitle2">
              <Grid container spacing={1}>
                {usuario.cobrancas.map((cobranca, index) => (
                  <Fragment key={cobranca.id}>
                    <Grid item xs={12} sm={4} md={3} textAlign={{ sm: 'right' }}>
                      <Box pr={3} pt={1} pb={1}>
                        Cobran√ßa #{cobranca.id}:
                      </Box>
                    </Grid>
                    <Grid item xs={12} sm={8} md={9} display="flex" alignItems="center">
                      <Text color="black">
                        <b>R$ {cobranca.valor.toFixed(2)} - Vencimento: {new Date(cobranca.data_vencimento).toLocaleDateString()} - Status: {cobranca.status}</b>
                        {cobranca.data_pagamento && <span> - Pago em: {new Date(cobranca.data_pagamento).toLocaleDateString()}</span>}
                      </Text>
                    </Grid>
                  </Fragment>
                ))}
              </Grid>
            </Typography>
          </CardContent>
        </Card>
      </Grid>

    </Grid>
  );
}

export default Content;
